@font-face {
    font-family: 'Laila';
    src: local('Laila') url(/static/media/Laila-Regular.b5161678.ttf) format('truetype');
}

@font-face {
    font-family: 'Philosopher';
    src: local('Philosopher') url(/static/media/Philosopher-Regular.af6ea627.ttf) format('truetype');
}

:root {
    --primary: #1C5781;
    --secondary: #C7268E;
    --body: #082E3E;
    --white: #FFFFFF;
    --gray1: #F4F4F4;
    --gray2: #BFC5C8;
    --gray3: #99A1A5;
    --gray4: #575C5F;
    --gray5: #3B4549;
    --background: #FBF5E9;
    --infored: #BD4E48;
    --gradient-blue: linear-gradient(90deg, rgba(28,87,129,1), rgba(96,150,188,1) 100%);
    --gradient-pink: linear-gradient(90deg, rgba(198,36,141,1), rgba(214,120,176,1) 100%);
    --primary-transparent: rgba(15, 147, 241, 0.2);
    --secondary-transparent: rgba(247, 11, 164, 0.2);
    --sidebar-width: 300px;


    color: #082E3E;


    color: var(--body);
}

*, *::before, *::after {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    font-family: 'Laila', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 16px;
    height: 100%;
}

#root {
    width: 100vw;
    height: 100vh;
}

#outlet {
    flex: 1 1;
    display: flex;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Philosopher';
    margin-top: 24px;
    margin-bottom: 12px;
    font-weight: 400;
}

.sidebarContainer + .MuiContainer-root {
    margin-left: 300px;
    margin-left: var(--sidebar-width);
}

button {
    height: 36px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-family: 'Laila';
    font-weight: 600;
    color: #082E3E;
    color: var(--body);
    background-color: #FFFFFF;
    background-color: var(--white);
    border: 2px solid #082E3E;
    border: 2px solid var(--body);
    cursor: pointer;
    border-radius: 24px;
    padding: 0px 36px 0px 36px;
    transition: 0.15s;
}

button.primary {
    background: linear-gradient(90deg, rgba(28,87,129,1), rgba(96,150,188,1) 100%);
    background: var(--gradient-blue);
    border: none;
    color: #FFFFFF;
    color: var(--white);
}

button.primary-stroke:not(:disabled) {
    color: #1C5781;
    color: var(--primary);
    background-image: linear-gradient(90deg, rgba(28,87,129,1), rgba(96,150,188,1) 100%);
    background-image: var(--gradient-blue);
    border: 3px solid transparent;
    box-shadow: 2px 1000px 1px #fff inset;
    background-origin: border-box;
}

button.primary-stroke:hover:not(:disabled), button.secondary-stroke:hover:not(:disabled) {
    box-shadow: none;
    color: #FFFFFF;
    color: var(--white);
}

button.secondary {
    background: linear-gradient(90deg, rgba(198,36,141,1), rgba(214,120,176,1) 100%);
    background: var(--gradient-pink);
    border: none;
    color: #FFFFFF;
    color: var(--white);
}

button.secondary-stroke {
    color: #C7268E;
    color: var(--secondary);
    background-image: linear-gradient(90deg, rgba(198,36,141,1), rgba(214,120,176,1) 100%);
    background-image: var(--gradient-pink);
    border: 3px solid transparent;
    box-shadow: 2px 1000px 1px #fff inset;
    background-origin: border-box;
}

button:disabled {
    background-color: #F4F4F4;
    background-color: var(--gray1);
    border-color: #BFC5C8;
    border-color: var(--gray2);
    color: #BFC5C8;
    color: var(--gray2)
}

input[type=text], input[type=password] {
    height: 36px;
    line-height: 36px;
    width: 100%;
    font-size: 16px;
    font-family: 'Laila';
    background: #FFFFFF;
    background: var(--white);
    border: 2px solid #BFC5C8;
    border: 2px solid var(--gray2);
    border-radius: 20px;
    padding: 0px 12px 0px 12px;
    transition: all .2s ease-in-out;
    outline: 0px solid #F4F4F4;
    outline: 0px solid var(--gray1);
}

input[type=text]::-webkit-input-placeholder, input[type=password]::-webkit-input-placeholder {
    color: #BFC5C8;
    color: var(--gray2);
}

input[type=text]:-ms-input-placeholder, input[type=password]:-ms-input-placeholder {
    color: #BFC5C8;
    color: var(--gray2);
}

input[type=text]::placeholder, input[type=password]::placeholder {
    color: #BFC5C8;
    color: var(--gray2);
}


input[type=text]:invalid:not(:-ms-input-placeholder), input[type=password]:invalid:not(:-ms-input-placeholder) {
    color: #BD4E48;
    color: var(--infored);
    border-color: #BD4E48;
    border-color: var(--infored);
}


input[type=text]:invalid:not(:placeholder-shown), input[type=password]:invalid:not(:placeholder-shown) {
    color: #BD4E48;
    color: var(--infored);
    border-color: #BD4E48;
    border-color: var(--infored);
}

input[type=password] {
    font-family: 'Verdana';
    font-size: 22px;
}

input[type=text]:focus-visible, input[type=password]:focus-visible {
    outline: 4px solid #F4F4F4;
    outline: 4px solid var(--gray1);
}

input[type=text]:focus, input[type=password]:focus {
    outline: 3px solid rgba(15, 147, 241, 0.2);
    outline: 3px solid var(--primary-transparent);
}

.labelInputContainer {
    display: flex;
    flex-direction: column;
}

.checkboxContainer {
    display: flex;
    height: 28px;
    margin-top: 24px;
    margin-bottom: 12px;
}

.checkboxContainer label {
    height: 28px;
    line-height: 28px;
}

textarea{
    height: 96px;
    font-size: 16px;
    font-family: 'Laila';
    background: #FFFFFF;
    background: var(--white);
    border: 2px solid #BFC5C8;
    border: 2px solid var(--gray2);
    border-radius: 20px;
    padding: 0px 12px 0px 12px;
    transition: all .2s ease-in-out;
    outline: 0px solid #F4F4F4;
    outline: 0px solid var(--gray1);
}
.checkboxContainer input[type='checkbox'] {
    height: 20px;
    width: 20px;
    margin: 4px 0px 4px 6px;
}

.checkboxContainer input[type='checkbox']:hover {
    cursor: pointer;
}

.checkboxContainer input[type='checkbox']:checked {
    color: #1C5781;
    color: var(--primary);
}

.checkboxContainer label {
    margin-top: 0px;
    margin-bottom: 0px;
}

label {
    font-weight: 500;
    margin-top: 24px;
    margin-bottom: 6px;
    margin-left: 6px;
}

hr {
    border-top: 2px solid #BFC5C8;
    border-top: 2px solid var(--gray2);
}


nav.topnav {
    padding: 12px 10px 12px 10px;
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: row;
}

nav.topnav .logo-home {
    width: 130px;
    min-width: 130px;
    height: 100%;
}

nav.topnav .logo-home a div {
    width: 100%;
    height: 100%;
}

nav.topnav .logo-home a div img {
    width: 100%;
}

nav.topnav ul {
    list-style-type: none;
    padding: 0px;
    margin: 0px;
    margin-right: 130px;
    flex-grow: 1;
    display: inherit;
    justify-content: center;
}

nav.topnav ul li {
    padding: 0px 8px 0px 8px;
    display: flex;
}

nav.topnav ul li a {
    color: var(--primary);
    text-decoration: none;
    text-underline-offset: 6px;
    margin-top: auto;
    margin-bottom: auto;
    text-align: center;
}

nav.topnav ul li a.active {
    text-decoration: underline;
}

nav.topnav ul li:nth-child(odd):hover {
    background-color: var(--gray1);
}

nav.topnav ul div {
    padding: 8px;
}

div.landing {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.landing img {
    max-width: 600px;
    margin-top: 130px;
}
.landing > hr {
