}
.videoCall {
    width: 100%;
}

.videoCall .videoControls {
    z-index: 1;
    position: relative;
    bottom: 18px;
}

.videoCall .videoControls button {
    margin: auto;
    display: block;
}

.videoCall .videoContainer {
    height: 60%;
    transition: 1s;

    background: rgb(96, 150, 188);
    background: radial-gradient(circle, rgba(96, 150, 188, 1) 0%, rgba(28, 87, 129, 1) 100%);

    display: flex;
    justify-content: center;
    align-items: middle;
    overflow-x: scroll;
}

.videoCall .videoContainer::-webkit-scrollbar {
    display: none;
}

.videoCall .videoContainer:empty {
    height: 100px;
}

.videoCall .videoContainer video {
    margin-top: auto;
    margin-bottom: auto;
}

.videoCall video {
    width: 250px;
    aspect-ratio: 9/6;
    object-fit: cover;
}

.videoCall video.cameraView {
    position: relative;
    top: -15px;
    left: 20px;
    z-index: 1;
    -webkit-filter: drop-shadow(3px 3px 3px var(--gray4));
            filter: drop-shadow(3px 3px 3px var(--gray4));
}

.authenticate > div {
    display: flex;
    justify-content: space-evenly;
    margin-top: 120px;
}

.authenticate > div > div {
    display:block;
}

.authenticate h1 {
    margin-left: 6px;
}

.authenticate .buttonContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.authenticate br {
    height: 32px;
}

.authenticate button {
    width: 70%;
    margin-top: 12px;
    margin-left: 15%;
    margin-right: 15%;
}

.authenticate .error {
    color: var(--infored);
}

@media only screen and (max-width: 900px) {
    .authenticate > div {
        margin-top: 0px;
    }
    .authenticate img {
        display: none;
    }
}

